import turfDistance from "@turf/distance";
import { point as turfPoint } from "@turf/helpers";

// Are some popular web platforms calculating the distances for their "Best
// Efforts" feature from rounded imperial distances converted to metric?
const ONE_K = 1;
const ONE_MILE = toKms(1);
const FIVE_K = 5; // toKms(3.1);
const FIVE_MILE = toKms(5);
const TEN_K = 10; // toKms(6.2);
const TEN_MILE = toKms(10);
const HALF_MARATHON = 21.0975; // toKms(13.1);
const MARATHON = 42.195; // toKms(26.2);
const FIFTY_K = 50;

function toKms (miles) {
  return miles * 1.60934;
}

function fractionOver (previous, target, over) {
  const d = over - previous;
  const n = target - previous;
  return n / d;
}

function lerpTime (prevDistance, targetDistance, overDistance, prevTime, overTime) {
  const t = fractionOver(prevDistance, targetDistance, overDistance);
  const diff = overTime - prevTime;
  return prevTime + diff * t;
}

/// Transform GPX points into a simpler list of distance,time,elevation points.
export function fromGpx (points) {
  points = points.map(({ lon, lat, ele, time }) => {
    return {
      point: turfPoint([lon, lat]),
      time: Date.parse(time),
      elevation: ele,
    }
  });

  const output = [];

  let elapsed = 0;
  let previous = points[0];
  for (const current of points) {
    const time = current.time - points[0].time;
    elapsed += turfDistance(previous.point, current.point, {
      units: "kilometers",
    });
    output.push({
      distance: elapsed,
      elevation: current.elevation,
      time: time,
    });
    previous = current;
  }

  return output;
}

/// Calculate the splits using a given multiple
export function splits (points, multiple, tolerance) {
  const output = [];

  let distance = multiple;
  let previous = points[0];
  let previousTime = 0;

  for (const current of points) {
    if (current.distance < distance) {
      continue;
    }

    const time = lerpTime(
      previous.distance,
      distance,
      current.distance,
      previous.time,
      current.time
    );

    output.push({
      time: time - previousTime
    });
    distance += multiple;
    previousTime = time;
  }

  const last = points[points.length - 1];
  if (last.distance >= distance - tolerance) {
    const t = fractionOver(previous.distance, last.distance, distance);
    const d = 1 + ((1 - t) / t);
    const diff = last.time - previous.time;
    const time = previous.time + diff * d;
    output.push({
      time: time - previousTime,
      estimated: true,
      shortBy: (distance - last.distance) * 1000,
    });
  }

  return output;
}

/// Calculate splits using common benchmark distances as a multiple
export function benchmarkDistances (points) {
  return {
    oneKs: splits(points, ONE_K, 0.05),
    oneMiles: splits(points, ONE_MILE, 0.05),
    fiveKs: splits(points, FIVE_K, 0.05),
    fiveMiles: splits(points, FIVE_MILE, 0.05),
    tenKs: splits(points, TEN_K, 0.05),
    tenMiles: splits(points, TEN_MILE, 0.05),
    halfMarathons: splits(points, HALF_MARATHON, 0.05),
    marathons: splits(points, MARATHON, 0.05),
    fiftyKs: splits(points, FIFTY_K, 0.05),
  };
}
