import React from "react";
import GpxParser from "./gpx";

import "./App.css";
import FileDropper from "./components/FileDropper.js";
import { fromGpx, benchmarkDistances } from "./running.js";

function msToTime(s) {
  var ms = s % 1000;
  s = (s - ms) / 1000;
  var secs = s % 60;
  s = (s - secs) / 60;
  var mins = s % 60;
  var hrs = (s - mins) / 60;

  hrs = hrs.toString().padStart(2, '0');
  mins = mins.toString().padStart(2, '0');
  secs = secs.toString().padStart(2, '0');
  return hrs + ':' + mins + ':' + secs;
}

export default class App extends React.Component {
  constructor() {
    super();
    this.state = {
      active: null,
      files: [],
    };
  }

  handleFiles(files) {
    const active = this.state.files.length;

    for (const file of files) {
      var reader = new FileReader();
      reader.readAsText(file, "utf-8");
      reader.onload = async (event) => {
        const gpx = new GpxParser()
        try {
          gpx.parse(event.target.result);
        } catch (err) {}

        var data = {
          name: file.name,
          error: false,
        }

        if (gpx.tracks.length >= 1) {
          const points = fromGpx(gpx.tracks[0].points);
          const extra = benchmarkDistances(points);

          data = Object.assign({}, data, extra);
        } else {
          data.error = true;
        }

        this.setState(state => {
          return Object.assign({}, state, {
            files: [...state.files, data],
            active: active,
          })
        });
      };
    }
  }

  handleFileClick(i) {
    this.setState(state => {
      return { active: i };
    });
  }

  renderFile(data, i) {
    const { active } = this.state;
    const prefix = 'app__sidebar__file';
    return (
      <button
        className={`${prefix} ${i === active ? prefix + '--active' : ''}`}
        key={`${data.name}-${i}`}
        onClick={event => this.handleFileClick(i)}
      >
        {data.name}
      </button>
    );
  }

  renderSplitsForDistance(header, splits) {
    if (splits.length === 0) {
      return;
    }

    const renderEstimate = (split) => {
      if (!split.estimated) {
        return;
      }
      return (
        <span className="estimate">{`${split.shortBy.toFixed(2)} meters`}</span>
      );
    };

    const hasEstimate = splits
      .filter(split => split.estimated === true)
      .length !== 0;

    return (
      <div className="distance">
        <h3 className="distance__header">{header}</h3>
        <table className="distance__table">
          <thead>
            <tr>
              <th></th>
              <th>Time</th>
              {hasEstimate && (<th>Estimated</th>)}
            </tr>
          </thead>
          <tbody>
            {splits.map((split, i) => {
              return (
                <tr key={i}>
                  <td>{i + 1}</td>
                  <td>{msToTime(split.time)}</td>
                  {hasEstimate && <td>{renderEstimate(split)}</td>}
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
    );
  }

  renderActiveFile(data) {
    const withError = () => (
      <p className="file__distances">
        File does not contain any tracks
      </p>
    );

    const withData = () => (
      <div className="file__distances">
        {/* this.renderSplitsForDistance("1k", data.oneKs) */}
        {this.renderSplitsForDistance("1M", data.oneMiles)}
        {this.renderSplitsForDistance("5k", data.fiveKs)}
        {/* this.renderSplitsForDistance("5M", data.fiveMiles) */}
        {this.renderSplitsForDistance("10k", data.tenKs)}
        {/* this.renderSplitsForDistance("10M", data.tenMiles) */}
        {this.renderSplitsForDistance("Half Marathon", data.halfMarathons)}
        {this.renderSplitsForDistance("Marathon", data.marathons)}
        {this.renderSplitsForDistance("50k", data.fiftyKs)}
      </div>
    );

    return (
      <div className="file">
        <h2 className="file__name">{data.name}</h2>
        {data.error === true ? withError() : withData()}
      </div>
    );
  }

  render() {
    const { files, active } = this.state;
    const activeFile = files[active];
    return (
      <div className="app">
        <div className="app__sidebar">
          <FileDropper onFilesDrop={files => this.handleFiles(files)}>
            Drop GPX files here
          </FileDropper>
          {files.map((file, i) => this.renderFile(file, i))}
        </div>
        <div className="app__content">
          {activeFile && this.renderActiveFile(activeFile)} 
        </div>
      </div>
    );
  }
}
